* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  font-size: 1.1rem;
  font-family: "IBM Plex Sans", sans-serif;
}

body,
input,
button {
  font-family: Roboto, sans-serif;
}

.pageContainer {
  height: 100vh;
  display: flex;
}

.content {
  height: 100%;
  flex: 1 1;
}

@media only screen and (max-width: 768px) {
  .content {
    height: 100vh;
    flex: 3 1;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
  cursor: pointer;
}

