header svg {
  width: 1vw;
}
header li {
  list-style-type: none;
  width: -webkit-max-content;
  width: max-content;
}

.bx--header{
  position: relative;
}

.bx--header__action{
  width: auto;
  padding-left: .5rem;
  padding-right: .5rem;
}

a.bx--header__menu-item:focus{
  border-color: transparent;
  outline: none;
}

.bx--header__menu {
  width: -webkit-max-content!important;
  width: max-content!important
}

.bx--header__menu li {
  width: 100% !important;
