  bottom: 0px;
  left: 0px;
  width: 100%;
}

#pagination{
  width: 100%;
}

#dateChooser{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  background-color: #f2f2f2;
}

.bx--tabs .bx--skeleton {
  margin-bottom: 2vh;
}

.bx--tabs__nav .bx--tabs__nav--hidden {
