}

.bx--tabs__nav-link {
  width: 15vw !important;
  height: 5vh !important;
}

.bx--skeleton.bx--data-table-container {
  margin-left: 1vw;
  margin-right: 1vw;
}

.bx--skeleton.bx--data-table-header {
  padding: 1rem 0px 0.5rem 1rem;
}

.bx--skeleton.bx--btn.bx--btn--sm {
  height: 20vh;
}

.bx--data-table-header__description {
  display: none;
}
